import searchSection from './common/search-section'

export default {
  props: ['hot', 'history'],
  components: {
    searchSection
  },
  methods: {
    selectHot (o) {
      this.$emit('select-hot', o)
    },
    selectHistory (o) {
      this.$emit('select-history', o)
    },
    clearHistory () {
      this.$emit('clear-history')
    }
  }
}



