export default {
  data () {
    return {
      keyword: this.searchKey,
      sort: 0
    }
  },
  props: ['step', 'searchKey'],
  computed: {
    searchBtnVisible () {
      return this.step !== 3
    },
    bottomVisible () {
      return this.step === 3
    }
  },
  methods: {
    search () {
      this.$emit('search', this.keyword)
    },
    clear () {
      this.keyword = ''
      this.$emit('clear')
    },
    changeSort (n) {
      this.sort = n
      this.$emit('change-sort', n)
    }
  },
  watch: {
    'keyword': function (val) {
      this.$emit('key-change', val)
    },
    'searchKey': function (val) {
      this.keyword = val
    }
  }
}



